import React from 'react'
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.css'
import Home from './components/pages/Home';
import About from './components/pages/About'
import Contact from './components/pages/Contact'
import Navbar from './components/layout/Navbar'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Notfound from './components/pages/Notfound';
import AddUser from './components/users/AddUser';
import EditUser from './components/users/EditUser';
import ViewUsers from './components/users/ViewUsers';

function App() {
  return (
<Router>
      <div className="App">
        <Navbar />
        <Switch>
          <Route exact path="/" component={Home}/>
          <Route exact path="/about" component={About}/>
          <Route exact path="/contact" component={Contact}/>

          <Route exact path="/users/AddUser" component={AddUser} />
          {/* below is dynamic routing by specifying the id as a parameter  */}
          <Route exact path="/users/EditUser/:id" component={EditUser} />
          <Route exact path="/users/ViewUsers/:id" component={ViewUsers}/>
          <Route component={Notfound}/>
        </Switch>

      </div>
</Router>
  );
}

export default App;
